.performance-row {
    display: flex;
    width: 100%;
    justify-content: space-between;
    border-bottom: 1px solid #cfcfcf;
}

.performance-row .position {
    width: 10%;
}

.performance-row .athlete {
    width: 70%;
}

.performance-row .performance {
    width: 20%;
    text-align: right;
}

.gold-medal {
    color: #ffd700;
}

.silver-medal {
    color: #808080;
}

.bronze-medal {
    color: #cd7f32;
}
