.meetingPage {
    display: flex;
    flex-direction: column;
}

name {
    font-size: 1.5rem;
    margin: 0 2.5vw;
}

.details {
    display: flex;
    justify-content: space-between;
    margin: 0 2.5vw;
}

.location {
    font-size: 1.2rem;
}

.performances {
    display: flex;
    flex-direction: column;
    align-self: center;
    width: 95vw;
}

.tableHeader {
    display: flex;
    justify-content: space-between;
    margin-top: 1vh;
    background-color: #cfcfcf;
    padding: 0 5px;
}

.tableHeader .event {
    font-weight: 600;
}

.filterPanel {
    display: flex;
    justify-content: space-between;
    margin: 0 2.5vw;
}
