.headToHead {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.athletes {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.athletes .athlete {
    display: flex;
    justify-content: stretch;
    align-items: center;
    text-align: center;
    width: 45vw;
}

.athleteInformation {
    width: 100%;
}

.subDetails {
    display: flex;
    font-size: 0.8rem;
    color: #888888;
}

.comparison {
    width: 95vw;
}

.statistics {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
}

.results {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.name {
    width: 100%;
    font-size: 1.2rem;
}

.subDetails {
    flex-direction: column;
}

.searchInputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 7vh;
}

.searchInputs input {
    width: 90%;
}

.cross {
    align-self: flex-start;
    margin: 2px;
    background-color: red;
    color: #ffffff;
    border-radius: 50%;
    font-size: 0.8rem;
    line-height: 8px;
    height: 10px;
    width: 10px;
}
